/* Fonts */

//Montserrat

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat Regular"),
    url("../fonts/montserrat/Montserrat-Regular400.woff2") format("woff2"),
    url("../fonts/montserrat/Montserrat-Regular400.woff") format("woff"),
    url("../fonts/montserrat/Montserrat-Regular400.ttf") format("truetype");
}
